@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-display: fallback;
  font-style: normal;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Open Sans";
  font-display: fallback;
  font-style: normal;
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  background-color: #FFFFFF;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  min-width: 320px;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.wrapper {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .wrapper {
    max-width: 1150px;
  }
}
.button {
  display: block;
  font: inherit;
  padding: 14px 0;
  width: 100%;
  max-width: 320px;
  border: solid 2px #444444;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: #444444;
  text-align: center;
  text-transform: uppercase;
  background-color: #FFFFFF;
  cursor: pointer;
  margin: 0 auto;
}
.button:hover,
.button:focus {
  color: #FFFFFF;
  background-color: #444444;
  outline: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-title {
  background-color: #F1F1F1;
  padding: 72px 0 74px;
  text-align: center;
  margin: 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 43px;
    line-height: 50px;
    padding: 36px 0 82px;
    position: relative;
  }
  .main-title::after {
    content: "";
    width: 100%;
    height: 23px;
    background-image: url("../../img/bg-zigzag-fill.svg");
    background-size: contain;
    position: absolute;
    bottom: -12px;
    left: -5px;
  }
}
@media (min-width: 1150px) {
  .main-title {
    font-size: 53px;
    line-height: 57px;
    padding-bottom: 105px;
  }
  .main-title::after {
    height: 34px;
    bottom: -12px;
    left: -5px;
  }
}
.starting {
  background-color: #F9F9F9;
  background-image: url("../../img/hero-mobile@1x.jpg");
  background-size: 575px 373px;
  background-repeat: no-repeat;
  background-position: center -15%;
  padding: 71px 0 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 489px;
}
@media (min-resolution: 144dpi) {
  .starting {
    background-image: url("../../img/hero-mobile@2x.jpg");
  }
}
.starting__title {
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  color: #000000;
  padding: 0 38px;
  text-align: center;
  margin-top: 0;
}
.starting__list {
  list-style: none;
  padding: 0;
  margin: 0;
  max-width: 100%;
  margin-top: 100px;
}
.starting__item--decor {
  background-color: #63D1BB;
}
.starting__item--toys {
  background-color: #56C3B2;
}
.starting__link {
  font-size: 19px;
  font-weight: 700;
  color: #FFFFFF;
  padding: 26px 29px 29px;
  display: block;
  position: relative;
}
.starting__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
}
.starting__link::after {
  content: "";
  position: absolute;
  opacity: 0.5;
  top: 50%;
  transform: translateY(-50%);
}
.starting__link--decor {
  background-color: #63D1BB;
}
.starting__link--decor:hover {
  background-color: #59d5bc;
}
.starting__link--decor:hover::before {
  background-color: #59d5bc;
}
.starting__link--decor::after {
  background-image: url("../../img/icon-interior.svg");
  width: 40px;
  height: 62px;
  right: 36px;
}
.starting__link--toys {
  background-color: #56C3B2;
}
.starting__link--toys:hover {
  background-color: #46C1AE;
}
.starting__link--toys:hover::before {
  background-color: #46C1AE;
}
.starting__link--toys::after {
  background-image: url("../../img/icon-toy.svg");
  width: 60px;
  height: 60px;
  right: 27px;
}
.starting__link:hover {
  padding-top: 21px;
  padding-bottom: 34px;
}
.starting__link:hover::after {
  opacity: 0.7;
  transform: translateY(calc(-50% - 5px));
}
.starting__link:hover::before {
  width: 100%;
  height: 5px;
  top: -5px;
}
.starting__link:active {
  color: rgba(255, 255, 255, 0.3);
}
.starting__link:active::after {
  opacity: 0.3;
}
.starting__text {
  max-width: 120px;
  display: block;
}
@media (min-width: 406px) {
  .starting__list {
    margin-top: 135px;
  }
}
@media (min-width: 768px) {
  .starting {
    background-image: url("../../img/hero-tablet@1x.jpg");
    background-size: 1150px 746px;
    background-position: center -60px;
    min-height: 685px;
    position: relative;
    padding-top: 95px;
  }
  .starting::after {
    content: "";
    height: 27px;
    max-height: 34px;
    width: 100%;
    background-image: url("../../img/bg-zigzag-fill.svg");
