    position: absolute;
    bottom: -15px;
    left: 0;
  }
  .starting__title {
    font-size: 43px;
    line-height: 50px;
    padding: 0 127px;
  }
  .starting__list {
    display: flex;
    padding: 0 50px;
    margin-top: 335px;
  }
  .starting__item {
    flex-grow: 1;
  }
  .starting__link {
    font-size: 20px;
    line-height: 30px;
    padding: 29px 35px 29px 37px;
  }
  .starting__link--toys::after {
    right: 35px;
  }
  .starting__text {
    max-width: 180px;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi) {
  .starting {
    background-image: url("../../img/hero-tablet@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .starting {
    background-image: url("../../img/hero-desktop@1x.jpg");
  }
  .starting::after {
    height: 37px;
    max-height: 37px;
    bottom: -19px;
    background-size: 1250px;
    background-position: 50%;
  }
  .starting__title {
    font-size: 53px;
    line-height: 57px;
    padding: 0 220px;
  }
  .starting__list {
    width: 1150px;
    margin: 315px auto 0;
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi) {
  .starting {
    background-image: url("../../img/hero-desktop@2x.jpg");
  }
}
.page-header {
  flex-grow: 1;
}
.page-header__logo-wrapper {
  position: relative;
}
.page-header__logo {
  display: block;
  padding: 22px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.page-header__logo[href]:hover {
  opacity: 0.6;
}
.page-header__logo[href]:active {
  opacity: 0.3;
}
.page-header__logo-image {
  display: block;
  width: 86px;
  height: 35px;
  margin: 0 auto;
}
.page-header__nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 9%;
  width: 20px;
  height: 14px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  background-image: url("../../img/icon-menu-open.svg");
  background-repeat: no-repeat;
}
.page-header__nav-button--closed {
  background-image: url("../../img/icon-menu-close.svg");
