  .page-header {
    position: relative;
  }
  .page-header::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    top: 88px;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.1);
  }
  .page-header__logo-wrapper {
    position: absolute;
    left: 50px;
  }
  .page-header__logo {
    border-bottom: none;
  }
  .page-header__logo-image {
    margin: 0;
    width: 112px;
