  }
  .page-header__nav-button,
  .page-header__nav-button--closed {
    display: none;
  }
}
@media (min-width: 1150px) {
  .page-header::before {
    content: none;
  }
  .page-header::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 65px;
    top: 88px;
    z-index: -10;
    background-color: #F1F1F1;
  }
  .page-header--index::after {
    background-color: #F9F9F9;
  }
  .page-header__logo-wrapper {
    top: 10px;
    left: 50%;
